import { FC } from 'react';
import { Wine } from '../../types/Wine';
import { ItemInfoList } from './ItemInfoList';
import { HeartComponent } from '../HeartComponent';
// @ts-ignore
import ReactReadMoreReadLess from 'react-read-more-read-less';
import styles from './ItemInfo.module.scss';

interface Props {
  wine: Wine;
}

export const ItemInfo: FC<Props> = ({ wine }) => {
  const {
    id,
    name,
    color,
    wine_type,
    country,
    region,
    brand,
    alcohol_percentage,
    description,
  } = wine;

  const wineInfo = [
    {
      term: '',
      definition: color,
    },
    {
      term: '',
      definition: wine_type,
    },
    {
      term: '',
      definition: country,
    },
    {
      term: '',
      definition: region,
    },
    {
      term: '',
      definition: brand,
    },
    {
      term: '',
      definition: description.name,
    },
    {
      term: '',
      definition: alcohol_percentage,
    },
    {
      term: '',
      definition: description.gastronomic,
    },
  ];

  return (
    <div className={styles.itemInfo}>
      <div className={styles.header}>
        <h2 className={styles.heading}>{name}</h2>
        <HeartComponent wineId={id} />
      </div>
      <div className={styles.itemInfoListWrap}>
        {wineInfo.map(({ term, definition }) => (
          <ItemInfoList key={term} term={term} definition={definition} />
        ))}
      </div>
      <div className={styles.description}>
        <ReactReadMoreReadLess
          charLimit={210}
          readMoreText=" "
          readLessText=""
          className="test"
        >
          {description.why_buy}
        </ReactReadMoreReadLess>
      </div>
    </div>
  );
};
